* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 10px;
  font-family: monospace;
}

#root {
  background: linear-gradient(#000 50%, blue);
}

header {
  height: 40vh;
  position: relative;
  overflow: hidden;
}

#bg {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  opacity: 0.7;
  background: linear-gradient(#aaa, #000 30%);
  z-index: 1;
}

h1 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 3rem;
  text-transform: uppercase;
  z-index: 2;
  color: #ccc;
  text-align: center;
}

.banerImg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-width: 100%;
}

main {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 80%;
  min-height: 50vh;
  margin: 2.5vh auto;
  background-color: darkblue;
  padding-top: 20px;
  padding-bottom: 20px;
  box-shadow: 0 0 8px 10px darkblue;
}

footer {
  min-height: 5vh;
  background: linear-gradient(#333, black);
  color: #ccc;
  text-align: center;
  line-height: 5vh;
  font-size: 1.5rem;
}

.input {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  width: 50%;
  padding: 10px;
}

.input label {
  display: block;
  width: 60%;
  font-size: 2rem;
  color: #ccc;
}

.input input {
  display: block;
  width: 20%;
  font-weight: bold;
  font-size: 1.7rem;
  background-color: #ccc;
  text-align: center;
}

.btn1 {
  display: block;
  padding: 10px 40px;
  margin: 30px;
  border: solid 2px #ccc;
  background-color: darkblue;
  font-size: 2rem;
  text-transform: uppercase;
  font-weight: bold;
  color: #ccc;
  cursor: pointer;
  transition: 0.5s;
}

.btn1:hover {
  background-color: #ccc;
  color: darkblue;
}

.result {
  display: flex;
  justify-content: space-around;
  width: 50%;
  font-size: 1.7rem;
  color: red;
}

@keyframes ball {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

.yellow {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  color: #000;
  font-weight: bold;
  font-size: 2rem;
  text-align: center;
  line-height: 50px;
  animation: ball 1s;
  background-color: yellow;
}

@media (max-width: 1024px) {
  main {
    width: 100%;
  }
}

@media (max-width: 768px) {
  h1 {
    font-size: 2.5rem;
  }

  .input label {
    font-size: 1.5rem;
    width: 70%;
  }

  .input input {
    width: 25%;
  }

  .input,
  .result {
    width: 95%;
  }

  .yellow {
    font-size: 1.5rem;
    width: 40px;
    height: 40px;
    line-height: 40px;
  }
}
